*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  display: block;
  font-family: 'Montserrat', sans-serif;
  font-size: 10px;
  line-height: 1;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: rgba(242, 230, 177, 1);
}


button {
  border: none;
  padding: 0;
  margin: 0;
}

body {
  max-width: 1280px;
  margin: 0 auto;
}

.gallery {
  display: flex;
  justify-content: center;
  grid-gap: 15px 20px;
  max-width: 1280px;
  margin: 80px auto;
  flex-wrap: wrap;
  transition: all 0.1s linear;

  @media (max-width: 500px) {
    margin: 80px 10px 40px 10px;
  }
}

.buttonBack {
  position: fixed;
  top: 5px;
  left: 40px;
  padding: 20px 0;
  display: block;
  background-color: transparent;
  cursor: pointer;
  font-size: 1.4rem;
  color: black;
  z-index: 9999;
}

.logo {
  font-family: 'Open Sans', sans-serif;
  font-size: 33px;
  color: rgba(242, 230, 177, 1);
  margin-left: 10px;
  font-weight: 300;
}

.gallery__item {
  display: flex;
  background-color: white;
  cursor: zoom-in;
  flex-direction: column;

  @media (max-width: 500px) {
    img {
      width: 100% !important;
    }
  }
}
.modal .wrapper {
  img {
    max-width: 100%;
    margin: 0 auto;
    transition: all 0.3s linear;
  }
}
.modal {
  padding: 0 10px;
  margin: 50px 0;
  display: none;
  justify-content: center;
  align-items: center;
  max-width: 1280px;
  transition: all 0.3s linear;
  opacity: 0;

  p {
    margin-top: 4px;
    margin-left: 1px;
    font-size: 16px;
    color: #9c9c9c;
  }
}

.close {
  position: fixed;
  top: 10px;
  right: 20px;
  font-size: 35px;
  color: #9c9c9c;
  background-color: transparent;
  cursor: pointer;

  :hover {
    color: #747474;
  }

  img {
    width: 25px;
    height: 25px;
  }
}

.center {
  align-self: center;
}

.hidden {
  display: none;
}

.active {
  display: flex;
  flex-direction: column;
}

.opacityOn {
  opacity: 1;
}

.opacityOff {
  opacity: 0;
}

.footer {
  margin-top: 90px;
  @media (max-width: 400px) {
    margin-top: 20px;
  }

  &__list {
    transition: all 0.3s linear;
    opacity: 1;
    display: flex;
    justify-content: center;

  }

  &__item {
    list-style: none;
    display: block;
    text-decoration: none;
    font-size: 16px;
    font-weight: 300;
    margin: 0 10px 40px 0;
    text-align: center;
    color: rgba(74, 85, 107, 1);

    @media (max-width: 400px) {
      font-size: 11px;
    }
  }
}

